.section {
  position: relative;
}

.section_features {
  padding: 60px 0;
  background-position: clamp(700px, 100%, 100%) 50% !important;
  background-repeat: no-repeat;
  background-size: auto 100% !important;
}

.section_count {
  padding: 60px 0;
  background-repeat: no-repeat;
  background-size: auto 100% !important;
}

.section_dealers {
  padding: 60px 0;
  background-repeat: no-repeat;
  background-size: auto 100%;
}

.section__content {
  max-width: clamp(700px, 40vw, 100%);
}

.section__content .title {
  margin-bottom: 40px;
}

.section__content .desc {
  overflow-wrap: break-word;
  font-size: 18px;
  padding-left: 10px;
  letter-spacing: 0;
  line-height: 1.5;
  margin-bottom: 2rem;
}

.section__content .desc .bignum {
  font-weight: 700;
  font-size: 3rem;
}

.section__content .desc .bigtext {
  font-weight: 700;
  font-size: 2rem;
}

.titlewhite .title {
  color: #fff;
}

.titleblue .title {
  color: #009ee3;
}

.textwhite .desc {
  color: #fff;
}

.textright {
  background-position: clamp(-700px, -1%, calc(100% - 700px)) 50% !important;
}

.textright .section__content {
  margin-left: auto;
}

.count {
  background-position: center;
  background-size: contain;
}

.angleleft-gray {
  position: relative;
}

.angleright-blue {
  position: relative;
}

.angleright-gray {
  position: relative;
}

.orange {
  color: #fff;
  background-color: #FC8356;
  background-size: 1100px auto !important;
  background-position: clamp(-1100px, -1%, calc(100% - 880px)) 50% !important;
}

.orange .title {
  color: white;
}

.wrap {
  display: grid;
  justify-items: center;
}

.section_dealers {
  background-position: center;
}

.section_dealers .title {
  color: white;
  margin-bottom: 59px;
}

.section_dealers .desc {
  color: white;
  font-size: 30px;
  max-width: 600px;
  padding-left: 10px;
  letter-spacing: -1.55px;
  line-height: 1.2;
}

.section_dealers .desc .bignum {
  font-weight: 700;
  font-size: 55px;
}

.section_dealers .desc .bigtext {
  font-weight: 700;
  font-size: 35px;
}

.blue {
  color: #fff;
  background-color: #009ee3;
}

.blue .title {
  color: white;
}

.bluewhite .section__content {
  padding: 7rem 2rem;
  font-size: 16px;
}

.bluewhite .section__content .form__block {
  margin: auto;
}

.bluewhite .section__content p {
  color: initial;
}

.bluewhite .section__content:first-child p {
  color: #fff;
}

.bluewhite.form .section__content:first-child:before {
  content: '';
  position: absolute;
  background-image: url(../img/bg-call.png);
  width: 50%;
  height: 100%;
  top: 0;
  left: 0;
  clip-path: polygon(0 0, 100% 0%, 90% 100%, 0% 100%);
  z-index: -1;
}

.bluewhite.form {
  background-position: 0 0;
  background-size: 100% auto;
  padding: 0;
  margin: 0;
}

.bluewhite.form h4 {
  font-family: 'squares';
  color: #fff;
  font-size: 26px;
}

.bluewhite.form span {
  font-family: 'squares';
  color: #fff;
  font-size: 40px;
}

.index-page .bc-m {
  display: none;
}

@media only screen and (min-width: 1024px) {
  .section_features {
    padding: 10rem 0;
  }

  .section_count {
    padding: 10rem 0;
  }

  .section_dealers {
    padding: 10rem 0;
  }

  .angleleft-gray::before {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #f9f9f9;
    top: -1px;
    left: 0;
    clip-path: polygon(100% 0, 0% 100%, 0 0);
  }

  .angleright-blue::after {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #009ee3;
    top: -169px;
    right: 0;
    z-index: 1;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  }

  .angleright-gray::after {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #fafafa;
    top: -169px;
    right: 0;
    z-index: 1;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  }

  .angleleft-blue::before {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #009ee3;
    top: -1px;
    left: 0;
    clip-path: polygon(100% 0, 0% 100%, 0 0);
  }

  .angleleft-orange::before {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #FC8356;
    top: -1px;
    left: 0;
    clip-path: polygon(100% 0, 0% 100%, 0 0);
  }

  .angleleft-white::before {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #fafafa;
    top: -1px;
    left: 0;
    clip-path: polygon(100% 0, 0% 100%, 0 0);
  }

  .angleright-orange::after {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #FC8356;
    top: -169px;
    right: 0;
    z-index: 1;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  }

  .angleright-white::after {
    content: '';
    position: absolute;
    width: 84px;
    height: 170px;
    background-color: #fafafa;
    top: -169px;
    right: 0;
    z-index: 1;
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
  }

  .wrap {
    grid-template: auto / 1fr 1fr;
  }
}

@media only screen and (max-width: 1023px) {
  .bluewhite .section__content {
    padding: 0 0 1rem;
  }

  .bluewhite .section__content .desc {
    text-align: center;
  }

  .form__block form {
    align-content: center;
    display: flex;
    flex-flow: column;
    align-items: center;
  }

  .bluewhite .section__content:first-child {
    padding-bottom: 7rem;
    padding-top: 4rem;
  }

  .bluewhite.form .section__content:first-child:before {
    width: 100%;
    clip-path: polygon(0 0, 100% 0, 100% 45%, 0 55%);
  }
}

@media only screen and (max-width: 768px) {
  .bluewhite.form span {
    font-size: 28px;
  }
}